:root {
  --color-primary: #2f3437;
  --color-button-red: #e34e4e;
  --color-button-blue: #1976d2;
  --bg-color-button-primary: #474c50;
  --bg-color-button-red: #E149771A;
  --bg-color-button-blue: #d4e8fb;
  --color-primary-variable: #2f3437;
  --color-button-red-variable: #e34e4e;
  --color-button-blue-variable: #1976d2;
  --bg-color-button-primary-variable: #474c50;
  --bg-color-button-red-variable: #E149771A;
  --bg-color-button-blue-variable: #d4e8fb;
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-primary-variable: #ffffffe6;
    --color-button-red-variable: #e34e4e;
    --color-button-blue-variable: #1976d2;
    --bg-color-button-primary-variable: #474c50;
    --bg-color-button-red-variable: #E149771A;
    --bg-color-button-blue-variable: #d4e8fb;
  }
}
.text {
  font-size: 16px;
  line-height: 1.65;
  padding: 0.5em 2px;
  margin: 0;
  font-weight: 500;
  box-sizing: border-box;
  font-family: ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, Noto Sans, sans-serif;
  color: var(--color-primary-variables);
  transition-duration: 0.3s;
}
.text[data-dark-mode=false] {
  color: var(--color-primary) !important;
}