:root {
  --color-primary: #2f3437;
  --color-button-red: #e34e4e;
  --color-button-blue: #1976d2;
  --bg-color-button-primary: #474c50;
  --bg-color-button-red: #E149771A;
  --bg-color-button-blue: #d4e8fb;
  --color-primary-variable: #2f3437;
  --color-button-red-variable: #e34e4e;
  --color-button-blue-variable: #1976d2;
  --bg-color-button-primary-variable: #474c50;
  --bg-color-button-red-variable: #E149771A;
  --bg-color-button-blue-variable: #d4e8fb;
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-primary-variable: #ffffffe6;
    --color-button-red-variable: #e34e4e;
    --color-button-blue-variable: #1976d2;
    --bg-color-button-primary-variable: #474c50;
    --bg-color-button-red-variable: #E149771A;
    --bg-color-button-blue-variable: #d4e8fb;
  }
}
.link-to {
  padding: 0.5em 2px 0 2px;
  margin: 0 0 0.5em 0;
  font-weight: 500;
  box-sizing: border-box;
  font-family: ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, Noto Sans, sans-serif;
  color: var(--color-primary-variable);
  text-decoration: none;
}
.link-to[data-dark-mode=false] {
  color: var(--color-primary);
}
.link-to--primary {
  border-bottom: 1px solid var(--color-primary-variable);
  font-size: 16px;
}
.link-to--button {
  padding: 12px;
  border-radius: 3px;
  line-height: 14px;
  transition-duration: 0.3s;
}
.link-to--button:hover, .link-to--button[data-active=true], .link-to--button[data-theme=button--primary]:hover {
  background-color: var(--bg-color-button-primary-variable);
}
.link-to--button:hover .link-to__text, .link-to--button[data-active=true] .link-to__text, .link-to--button[data-theme=button--primary]:hover .link-to__text {
  color: #fff;
}
.link-to--button[data-theme=button--red]:hover, .link-to--button[data-theme=button--red][data-active=true] {
  background-color: var(--bg-color-button-red-variable);
}
.link-to--button[data-theme=button--red]:hover .link-to__text, .link-to--button[data-theme=button--red][data-active=true] .link-to__text {
  color: var(--color-button-red-variable);
}
.link-to--button[data-theme=button--blue]:hover, .link-to--button[data-theme=button--blue][data-active=true] {
  background-color: var(--bg-color-button-blue-variable);
}
.link-to--button[data-theme=button--blue]:hover .link-to__text, .link-to--button[data-theme=button--blue][data-active=true] .link-to__text {
  color: var(--color-button-blue-variable);
}
.link-to__text {
  transition-duration: 0.3s;
}